.right-side {
    width: 100%;
    height: 100%;
    background-color: #191722;
    padding-top: 50px;
    padding-right: 50px;
}

.right-side ul {
    color: #ddd;
    list-style: none;
    overflow-y: scroll;
    padding-right: 15px;
    height: 100%;
    width: 100%;
    box-sizing: content-box;
}

.right-side .hide-bar {
    height: 60vh;
    width: 100%;
    overflow: hidden;
}

.right-side li {
    display: flex;
    align-items: center;
    width: 100%;
    height: 50px;
    margin-bottom: 20px;
    padding-left: 15px;
    padding-right: 15px;
    cursor: pointer;
    transition-duration: 0.4s;
}

.right-side li:hover {
    color: #40a16c !important;
    background-color: #0000002d;
    transition-duration: 0.2s;
}

.duration {
    margin-left: auto;
    margin-right: 20px;
}

.song-name {
    padding-left: 20px;
}

.active {
    color: #40a16c !important;
}

.copyright p {
    color: #9b9a9f;
    font-weight: 100;
    font-size: 12px;
    margin-left: 12%;
    margin-bottom: 0 !important;
    margin-top: 20px;
}