.middle {
    background-color: #191722;
    width: 100%;
    height: 100%;
    text-align: center;
}

.cover {
    width: 70%;
    padding-top: 50px;
}

.middle button {
    margin-top: 20px;
    width: 30%;
    border-radius: 999px;
    margin-bottom: 20px;
}

.middle h2{
    color: #fefeff;
    margin-top: 10px;
}

.middle .actor{
    color: #a1a0a6;
    font-weight: 300
}

.middle .remove{
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.middle .remove h6{
    color: #439e65;
    font-weight: 100;
}

.middle .remove .icon{
    margin-left: 20px;
    font-size: 15px;
    cursor: pointer;
}