.left-container {
    background-color: #06050a;
    width: 100%;
    height: 100%;
    padding-right: 20px;
    padding-top: 20px;
    position: relative;
}

h6 {
    color: #7d7c81;
    font-weight: 100;
}

.recently {
    margin-left: 20px;
}


.list-unstyled ul {
    padding-left: 0 !important
}

.logo {
    width: 60%;
    height: auto;
    float: left;
    margin-left: 20px;
}

.menu {
    padding-left: 0 !important;
    margin-top: 20px;
}

.menu li {
    padding-bottom: 10px;
    font-weight: 500;
    display: flex;
    padding-left: 20px
}

.menu a{
    color: #7d7c81;
}

.menu a:hover {
    color: #ffffff;
    transition-duration: .5s;
    cursor: pointer;
    text-decoration: none;
}

.left-container ul {
    list-style: none;
    color: #7d7c81;
}


.media a .media-body{
    font-size: 1em !important;
    font-weight: 400 !important;
    color: #7d7c81;
}

.media a .media-body h5 {
    font-size: 1em !important;
    font-weight: 400 !important;
}

.media a:hover{
    text-decoration: none;
}

.bottom-thing {
    position: absolute;
    bottom: 100px;
    width: 100%;
    padding-right: 20px;
    padding-left: 20px;
    right: 0;
}

.profile {
    display: flex;
    align-items: center;
    padding-top: 10px;
    border-top: 1px solid #ffffff;
}

.bottom-thing .username {
    color: white;
    padding-left: 20px;
}

.bottom-thing .avatar {
    width: 35px;
    height: 35px;
    border-radius: 100%;
}

.media {
    padding-left: 20px;
    margin-bottom: 20px;
}

.media a .media-body:hover {
    color: #ffffff !important;
    transition-duration: .5s;
    cursor: pointer;
}

.active-media h5 {
    color: #ffffff
}

.active-media {
    border-left: 2px solid #439e65;
}

.active-menu{
    color: #ffffff;
    border-left: 2px solid #439e65;
}

.active-menu a{
    color: #ffffff !important;
}