.bottom{
    height: 100%;
    width: 100%;
    background-color: #282828;
}
.bottom .cover{
    height: 60px;
    width: 60px;
    background-size: cover;
    margin-left: 20px;
    margin-right: 20px;
}
.bottom .info{
    display: flex;
    align-items: center;
    height: 100%;
}
.bottom .info h5{
    color: #ddd;
    font-weight: 400;
    font-size: 15px;
}
.bottom .info h6{
    color: #ddd;
    font-weight: 100
}

.bottom .progress-bar{
    background-color: #b2b2b2 !important
}

.bottom .progress{
    height: 0.2rem !important;
    background-color: #404040 !important;
    width: 60%;
    margin-left: 10px;
    margin-right: 10px;
}
.control-button{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 50%;
    font-size: 15px;
    margin-bottom: 10px;
    margin-top: 10px;
    margin-left: 50%;
    transform: translateX(-50%);
}

.play-bar{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.bottom .bar{
    display: flex;
    align-items: center;
    justify-content: center;
    color: #b2b2b2;
}
.control-thing{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.control-thing .progress{
    width: 30% !important;
}
.control-thing svg {
    margin-right: 10px
}

.play-btn{
    font-size: 30px !important;
}
.bottom .control-button .icon, .bottom .control-thing .icon {
    color: #b3b3b3;
}

.bottom .control-button .icon:hover, .bottom .control-thing .icon:hover {
    transform: scale(1.1);
    transition-duration: 0.2s;
    cursor: pointer;
    color: #ddd;
}

.checkIcon{
    margin-left: 20px;
    cursor: pointer;
}

.checkIcon:hover{
    transform: scale(1.1);
    transition-duration: 0.2s;
}