body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100vh;
}

.App .col-md-2, .App .col-md-4, .App .col-md-6, .App .col-md-10, .App .col-md-5 , .App .col-md-7{
  padding: 0 !important;
  height: 100%;
}

.row{
  margin: 0 !important
}

.player-bar{
  height: 80px;
  width: 100%;
  position: absolute;
  bottom: 0;
}
.left-container {
