}

.media {
    padding-left: 20px;
    margin-bottom: 20px;
}

.media a .media-body:hover {
    color: #ffffff !important;
    -webkit-transition-duration: .5s;
            transition-duration: .5s;
    cursor: pointer;
}

.active-media h5 {
    color: #ffffff
}

.active-media {
    border-left: 2px solid #439e65;
}

.active-menu{
    color: #ffffff;
