}

.active-menu a{
    color: #ffffff !important;
}
.middle {
    background-color: #191722;
    width: 100%;
    height: 100%;
    text-align: center;
}

.cover {
    width: 70%;
    padding-top: 50px;
}

.middle button {
    margin-top: 20px;
