}

.middle h2{
    color: #fefeff;
    margin-top: 10px;
}

.middle .actor{
    color: #a1a0a6;
    font-weight: 300
}

.middle .remove{
    margin-top: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    
}

