.right-side {
    width: 100%;
    height: 100%;
    background-color: #191722;
    padding-top: 50px;
    padding-right: 50px;
}

.right-side ul {
    color: #ddd;
    list-style: none;
    overflow-y: scroll;
    padding-right: 15px;
    height: 100%;
    width: 100%;
    box-sizing: content-box;
}

.right-side .hide-bar {
    height: 60vh;
    width: 100%;
    overflow: hidden;
}

.right-side li {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: 50px;
    margin-bottom: 20px;
    padding-left: 15px;
    padding-right: 15px;
    cursor: pointer;
    -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s;
}

.right-side li:hover {
    color: #40a16c !important;
    background-color: #0000002d;
    -webkit-transition-duration: 0.2s;
            transition-duration: 0.2s;
}

.duration {
    margin-left: auto;
    margin-right: 20px;
}

.song-name {
    padding-left: 20px;
}

.active {
    color: #40a16c !important;
}

.copyright p {
    color: #9b9a9f;
    font-weight: 100;
    font-size: 12px;
    margin-left: 12%;
    margin-bottom: 0 !important;
    margin-top: 20px;
}
.bottom{
    height: 100%;
    width: 100%;
    background-color: #282828;
}
.bottom .cover{
    height: 60px;
    width: 60px;
    background-size: cover;
    margin-left: 20px;
    margin-right: 20px;
}
.bottom .info{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
}
.bottom .info h5{
    color: #ddd;
    font-weight: 400;
    font-size: 15px;
}
.bottom .info h6{
    color: #ddd;
    font-weight: 100
}

.bottom .progress-bar{
    background-color: #b2b2b2 !important
}

.bottom .progress{
    height: 0.2rem !important;
    background-color: #404040 !important;
    width: 60%;
    margin-left: 10px;
    margin-right: 10px;
}
.control-button{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    width: 50%;
    font-size: 15px;
    margin-bottom: 10px;
    margin-top: 10px;
    margin-left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.play-bar{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.bottom .bar{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    color: #b2b2b2;
}
.control-thing{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
}
.control-thing .progress{
    width: 30% !important;
}
.control-thing svg {
    margin-right: 10px
}

.play-btn{
    font-size: 30px !important;
}
.bottom .control-button .icon, .bottom .control-thing .icon {
    color: #b3b3b3;
}

.bottom .control-button .icon:hover, .bottom .control-thing .icon:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition-duration: 0.2s;
            transition-duration: 0.2s;
    cursor: pointer;
    color: #ddd;
}

.checkIcon{
    margin-left: 20px;
    cursor: pointer;
}

.checkIcon:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition-duration: 0.2s;
            transition-duration: 0.2s;
}
.album {
    background-color: #191722;
    width: 100%;
    height: 100%;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
}

.loading {
    height: 100%;
    width: 100%;
    background-color: #000000;
    position: relative;
}

.loading .img-load {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 300px;
    height: auto;
    margin-top: -250px;
    /* Half the height */
    margin-left: -150px;
    /* Half the width */
    color: #ddd;
    text-align: center;
}

.loading .imgLoad{
    width: 100%;
}

.home {
    background-color: #000000;
    text-align: center;
    color: #ddd;
    width: 100%;
    height: 100%;
    padding-top: 20px;
}

.home img {
    margin: auto;
    margin-top: 20px;
    width: 700px;
}

.home h1{
    padding-top: 50px
}

.album .col-md-2 {
    padding-left: 5px !important;
    padding-right: 5px !important;
}

.album h1 {
    color: #ddd;
    margin-top: 10px;
    padding-bottom: 10px;
}

.album .card h6 {
    color: #ddd;
    font-size: 12px;
}

.album .card p {
    color: #989898
}

.album .col-md-2 a:hover {
    text-decoration: none;
}

