}

.album .card:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
    color: #ddd;
    cursor: pointer;
    box-shadow: 0px 10px 15px #000000
}

.album .card .card-img-top {
