}

.album-wrap{
    width: 100%;
    height: 180px;
    overflow-x: hidden;
}
.search{
    background-color: #191722;
    width: 100%;
    height: 100%;
}

.search>div>input{
    border-radius: 0;
    height: 60px;
    font-size: 28px;
    padding-left: 30px;
    padding-right: 30px;
    background-color: #282828 !important;
    border: none;
    outline: none !important;
    color: #ddd;
    width: 100%;
    caret-color: #439e65;
}

.search>div>input:focus{
    outline: none !important
}
.search .intro{
    text-align: center;
    color: white;
    padding-top: 20%;
}


.search-container{
    margin-top: 50px;
}

.menu-search{
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding-left: 0 !important;
}

.menu-search a {
    color: #b3b3b3;
    text-decoration: none;
    font-size: 20px;
    position: relative;
    padding-bottom: 5px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 100;
    padding-left: 50px;
    padding-right: 50px;
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;

}

.menu-search a:hover {
    color: #fff;
    text-decoration: none;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s
}

.menu-search a.active {
    color: #fff !important;
}
.menu-search a.active::before {
    content: "";
    height: 2px;
    background-color: #1db954;
    width: 30px;
    position: absolute;
    display: block;
    left: 50%;
    bottom: 0;
    margin-left: -15px;
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
}


.search .card h6 {
    color: #ddd;
    font-size: 12px;
}

.search .card p {
    color: #989898
}

.search .col-md-2 a:hover {
    text-decoration: none;
}

.search .col-md-4 {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.search .card {
    font-size: 12px;
    background-color: #191722;
    border: none;
    text-align: center;
    width: 70%;
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
    

}

.search .card:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
    color: #ddd;
    cursor: pointer;
    box-shadow: 0px 10px 15px #000000;
    position: relative;
    z-index: 999;
}

.search .card .card-img-top {
    width: 100%;
    height: auto;
}

.search-content{
    height: calc(100vh - 235px);
    overflow-y: auto;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 60px;
}
.notFound{
    color: #ddd;
    text-align: center;
    padding-top: 100px
}


.containerSearching{
    height: calc(100% - 135px);
    width: 100%;
}

.searching {
    height: 100%;
    width: 100%;
    position: relative;
    background-color: #000000;
}

.searching .img-load {
    position: absolute;
    top: 15%;
    left: 50%;
    margin-left: -250px;
    color: #ddd;
    text-align: center;
}

.img-size{
    width: 500px;
    height: auto;
}

.search-content .right-side{
    padding-top: 0 !important
}

