.album {
    background-color: #191722;
    width: 100%;
    height: 100%;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
}

.loading {
    height: 100%;
    width: 100%;
    background-color: #000000;
    position: relative;
}

.loading .img-load {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 300px;
    height: auto;
    margin-top: -250px;
    /* Half the height */
    margin-left: -150px;
    /* Half the width */
    color: #ddd;
    text-align: center;
}

.loading .imgLoad{
    width: 100%;
}

.home {
    background-color: #000000;
    text-align: center;
    color: #ddd;
    width: 100%;
    height: 100%;
    padding-top: 20px;
}

.home img {
    margin: auto;
    margin-top: 20px;
    width: 700px;
}

.home h1{
    padding-top: 50px
}

.album .col-md-2 {
    padding-left: 5px !important;
    padding-right: 5px !important;
}

.album h1 {
    color: #ddd;
    margin-top: 10px;
    padding-bottom: 10px;
}

.album .card h6 {
    color: #ddd;
    font-size: 12px;
}

.album .card p {
    color: #989898
}

.album .col-md-2 a:hover {
    text-decoration: none;
}

.album .card {
    font-size: 12px;
    background-color: #191722;
    border: none;
    text-align: center;
}

.album .card:hover {
    transform: scale(1.1);
    transition-duration: 0.3s;
    color: #ddd;
    cursor: pointer;
    box-shadow: 0px 10px 15px #000000
}

.album .card .card-img-top {
    height: 100%;
    width: auto;
}

.album-wrap{
    width: 100%;
    height: 180px;
    overflow-x: hidden;
}